.callback {
  position: relative;
  background-color: #00b2ff;
}

.callback::before {
  content: "";
  position: absolute;
  display: block;
  width: 51%;
  height: 100%;
  background-color: rgba(4, 24, 39, 0.5);
  clip-path: polygon(99% 0, 0% 100%, 0 0);
}
@media (max-width: 1023px) {
  .callback::before {
    display: none;
  }
}

.callback::after {
  content: "";
  position: absolute;
  top: 29%;
  left: 18%;
  display: block;
  width: 510px;
  height: 285px;
